import {
  Box,
  Grid,
  GridItem,
  Heading,
  Image,
  SimpleGrid,
  useColorModeValue,
} from "@chakra-ui/react";
import React from "react";
import styles from "./Css/Home.module.css";

export default function Home() {
  return (
    <Box className={styles.homeBody}>
      <Heading
        id="home"
        fontSize={{ base: "100px", sm: "160px", md: "250px" }}
        color={useColorModeValue("#F0F0F0", "gray.600")}
        className={styles.homeHeading}
        fontFamily="'Playfair Display', serif"
      >
        Home
      </Heading>
      <SimpleGrid
        columns={{ sm: 1, md: 2 }}
        spacing="0px"
        id="home"
        className={styles.home}
      >
        <Box
          //  bg="tomato"
          className={styles.homeLeft}
          height="550px"
        >
          <Box className={styles.subHeading}>
            <Heading as="h3">Hello!</Heading>
            <Heading as="h1">
              {" "}
              <span>I'm</span>
              <span> Ashish Kohad </span>
            </Heading>
            <Heading as="h2">Full Stack Web Developer</Heading>
          </Box>
        </Box>

        <Box
          // bg="green.300"
          height="550px"
          className={styles.homeRight}
        >
          <Image src={"image/ProfilePhoto.png"} maxW="300px" />
        </Box>
      </SimpleGrid>
    </Box>
  );
}
