import Carousel from "react-elastic-carousel";
import Item from "./Item";
import styles from "./styles.module.css";
import miniprojectData from "../MiniProjects.json";
import {
  Avatar,
  AvatarGroup,
  Box,
  Button,
  Heading,
  Image,
  Link,
  SimpleGrid,
  Text,
  useColorModeValue,
} from "@chakra-ui/react";
import { BsGithub, BsLink45Deg } from "react-icons/bs";

export default function CarouselScroll() {
  return (
    <Box
      m="auto"
      h="auto"
      maxW="1200px"
      mt="50px"
      className={styles.carousel_wrapper}
    >
      <Carousel
        color={useColorModeValue("#F0F0F0", "gray.600")}
        initialActiveIndex={1}
        enableAutoPlay
        autoPlaySpeed={3000}
        m="auto"
      >
        {miniprojectData.map((item) => (
          <Item key={item}>
            {/* <Box>
                <Image src={item.coverImage} width="400px" height="200px"/>
                <Heading>{item.title}</Heading>
              </Box> */}
            <SimpleGrid
              columns={{ base: 1, sm: 1, md: 2 }}
              spacing="5px"
              maxW="1200px"
              className={styles.container}
            >
              <Box bg="" height="auto" pl="10px">
                <Image
                  borderRadius="20px"
                  m="auto"
                  h="300px"
                  src={item.coverImage}
                />
              </Box>

              <Box
                bg=""
                height="auto"
                textAlign="left"
                pl="10px"
                pr="10px"
                className={styles.CarouselRight}
              >
                <SimpleGrid
                  columns={{ sm: 1, md: 2 }}
                  spacing="5px"
                  maxW="1200px"
                  className={styles.container}
                  textAlign="left"
                  // bg="blue.300"
                >
                  <Box className={styles.pipe1}>
                    <button className={styles.pipe}>{item.status}</button>
                  </Box>
                  <Box className={styles.pipe1}>
                    <button className={styles.pipe}>{item.type}</button>
                  </Box>
                  {/* <button className={styles.pipe}>{item.status}</button> */}
                </SimpleGrid>

                {/* TechStack */}
                <SimpleGrid
                  className={styles.TechStack}
                  columns={{ base: 2, sm: 2, md: 5 }}
                  spacing="5px"
                >
                  {item.techStack.map((tech) => (
                    <Box
                      bg="#2B6CB0"
                      height="30px"
                      key={tech + Date.now() + Date()}
                    >
                      {tech}
                    </Box>
                  ))}
                </SimpleGrid>

                {/* Title */}
                <Box className={styles.TitleDes}>
                  <Heading color="black" as="h1" mt="0px">
                    {item.title}
                  </Heading>
                  <Text color="black" as="p" mt="10px">
                    {item.description}
                  </Text>
                </Box>

                {/* links */}

                <SimpleGrid
                  columns={{ base: 1, sm: 2, md: 2 }}
                  className={styles.githubButton}
                  mb="10px"
                  mt="20px"
                >
                  <Box>
                    <Link href={item.githubLink} target="_blank">
                      <Button colorScheme="purple">
                        Github{" "}
                        <span>
                          {" "}
                          <BsGithub />
                        </span>{" "}
                      </Button>
                    </Link>
                  </Box>

                  <Box>
                    <Link href={item.deployLink} target="_blank">
                      <Button colorScheme="purple">
                        Deployemnet{" "}
                        <span>
                          <BsLink45Deg />
                        </span>{" "}
                      </Button>
                    </Link>
                  </Box>
                </SimpleGrid>
              </Box>
            </SimpleGrid>
          </Item>
        ))}
      </Carousel>
    </Box>
  );
}
